import axios from 'axios';

export default class Backendapi {
    getMessages(query){
        console.log(query);

        // create connection with the resultws we get via Heroku
        const path = 'https://fnt-backend.herokuapp.com/showinfo';
        return axios.post(path, {query}).then((res) => {
           return res.data

        })
        .catch((error) => {
            console.error(error);
        });
    }
} 